var WholeBnContent = require('./components/common/WholeBannerInfo.react');
var HeaderNav = require('./components/common/Header.react');

var _defaultPro = {
    id: 0,
    name: "[重要公告]這是一則的訊息!!!",
    link: "http://www.666.dead.you"
}

var datas = (typeof _promoNoteData !== 'undefined' && _promoNoteData.length > 0) ? _promoNoteData[0] : _defaultPro;

ReactDOM.render(
    <WholeBnContent content={datas} />,
    document.getElementById('wholeBanner')
);


ReactDOM.render(
    <HeaderNav/>,
    document.getElementById('header')
);



