var SearchNavBox = require('./HeaderSearch.react');
var AccountNavBox = require('./HeaderAccount.react');
var CartNavBox = require('./HeaderCart.react');
var MenuNavBox = require('./HeaderMenu.react');
var NEProduct = require('./Product.react');
/*var TrackStore = require('../../stores/cart/TrackStore');
var TrackAction = require('../../actions/cart/TrackAction');
var MenuStore = require('../../stores/common/MenuStore');
var MenuAction = require('../../actions/common/MenuAction');*/
var TrackStore = nemReact.require('TrackStore');
var TrackAction = nemReact.require('TrackAction');
var MenuStore = nemReact.require('MenuStore');
var MenuAction = nemReact.require('MenuAction');
var NetwTools = nemReact.require('NetwTools');
//var React = require('react');
//var ReactCSSTransitionGroup = React.addons.CSSTransitionGroup;

var NONE_NAV = 'noneNav';
var SEARCH_NAV = 'searchNav';
var ACCOUNT_NAV = 'accountNav';
var CART_NAV = 'cartNav';
var MENU_NAV = 'menuNav';
var SHOWNAVBOX = 'show';
var HIDENAVBOX = '';

function getTrackNumber() {
    var trackData = TrackStore.getAll();
    var trackNumber = 0;

    if (jQuery.isArray(trackData)) {
        for (var i = 0; i < trackData.length; i++) {
            if (trackData[i].stu == 0 || trackData[i].stu == 100 || trackData[i].stu == 101 || trackData[i].stu == 102) {
                trackNumber++;
            }
        }
    }

    return trackNumber;
}

var HeaderNav = React.createClass({
    getInitialState: function(){
        return {
            nowState: ''
        };
    },

    getActivityHeader: function(act) {
        switch (act) {
            case SEARCH_NAV:
                if (this.state.headerShowNav === SEARCH_NAV) {
                    this.setState({headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX});
                } else {
                    this.setState({headerShowNav: SEARCH_NAV, showNavBox: SHOWNAVBOX});
                }
                break;
            case ACCOUNT_NAV:
                if (NetwTools.getEamil() != "") {
                    if (this.state.headerShowNav === ACCOUNT_NAV) {
                        this.setState({headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX});
                    } else {
                        this.setState({headerShowNav: ACCOUNT_NAV, showNavBox: SHOWNAVBOX});
                    }
                } else {
                    location.href = nemReact.generateUrl("login");
                }
                break;
            case CART_NAV:
                if (this.state.headerShowNav === CART_NAV) {
                    this.setState({headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX});
                } else {
                    this.setState({headerShowNav: CART_NAV, showNavBox: SHOWNAVBOX});
                }
                break;
            case MENU_NAV:
                if (this.state.headerShowNav === MENU_NAV) {
                    this.setState({headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX});
                } else {
                    this.setState({headerShowNav: MENU_NAV, showNavBox: HIDENAVBOX});
                    this.activeMainMenu();
                }
                break;
            default:
                this.setState({headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX});
        }

    },

    activeMainMenu: function() {
        ReactDOM.render(
            <MenuNavBox close={this.getActivityHeader.bind(this, MENU_NAV)}/>, document.getElementById('mainMenu'));
    },

    componentDidMount: function() {
        TrackStore.addChangeListener(this._onTrackChange);
        TrackAction.getCart([], []);
        this.getActivityHeader(NONE_NAV);
        MenuAction.getHome();
    },

    componentWillMount: function() {
        this.setState({trackNumber: getTrackNumber()});
    },

    getInitialState: function() {
        return {headerShowNav: NONE_NAV, showNavBox: HIDENAVBOX, trackNumber: 0};
    },

    componentWillUnmount: function() {
        TrackStore.removeChangeListener(this._onTrackChange);
    },

    checkCurrentClass: function(obj){
        var checkNowState = this.state.headerShowNav,
            currentClass='';

        currentClass = (checkNowState != NONE_NAV && checkNowState == obj) ? " active" : "";
        return currentClass;
    },

    render: function() {
        var showNav = this.state.headerShowNav;
        var navBox;
        switch (showNav) {
            case SEARCH_NAV:
                navBox = <SearchNavBox/>;
                break;
            case ACCOUNT_NAV:
                navBox = <AccountNavBox/>;
                break;
            case CART_NAV:
                navBox = <CartNavBox/>;
                break;
            case MENU_NAV:
                /*navBox = <MenuNavBox close={this.getActivityHeader.bind(this, MENU_NAV)}/>;*/

                break;
            default:
        }
        return (
            <div className="header">
                <div className="navs">
                    <div className="btn btnMainMenu" onClick={this.getActivityHeader.bind(this, MENU_NAV)}></div>
                    {/*<div className="btn btnMainMenu" onClick={this.activeMainMenu}></div>*/}
                    <div className={"btn btnGroup btnSearch" + this.checkCurrentClass(SEARCH_NAV)} onClick={this.getActivityHeader.bind(this, SEARCH_NAV)}>search</div>
                    <h1><a href="/">LOGO</a></h1>
                    <div className={"btn btnGroup btnAccount" + this.checkCurrentClass(ACCOUNT_NAV)} onClick={this.getActivityHeader.bind(this, ACCOUNT_NAV)}>
                        Account {/*<span className="nums">99</span>*/}
                    </div>
                    <div className={"btn btnGroup btnShop" + this.checkCurrentClass(CART_NAV)} onClick={this.getActivityHeader.bind(this, CART_NAV)}>
                        shopCart
                        <span className="nums">{this.state.trackNumber}</span>
                    </div>
                </div>
                <div className={'navBox ' + this.state.showNavBox} onClick={this.getActivityHeader.bind(this, NONE_NAV)}>
                    {/*<ReactCSSTransitionGroup transitionName="exampleTTT" transitionAppear={true} transitionAppearTimeout={500}>*/}
                    {navBox}
                    {/*</ReactCSSTransitionGroup>*/}
                </div>
            </div>
        );
    },

    _onTrackChange: function() {
        this.setState({trackNumber: getTrackNumber()});
    }
});

module.exports = HeaderNav;



