/*import LazyLoad from 'react-lazy-load';*/
var TrackAction = nemReact.require('TrackAction');

var ERRORIMGSRC = "/Themes/2016/images/img_LoadingErr.jpg";
var LOADIMGSRC = "/Themes/2016/images/img_Loading.jpg";

var ProductSlogan = React.createClass({

    render: function() {
        return (
            <div class="prodTit">
                {this.props.slogan}
            </div>
        );
    }
});

var ProductShowLeft = React.createClass({

    render: function() {
        return (
            <p class="restOf">
                剩下<span>{this.props.qty}</span>個
            </p>
        );
    }
});

var ProductComingSoon = React.createClass({

    render: function() {
        return (
            <p class="notSaletext">
                敬請期待
            </p>
        );
    }
});

var ProductDisCount = React.createClass({

    render: function() {
        return (
            <div className="tagDiscount">
                <span className="discountNums">{this.props.discountNums}</span>
                <span>折</span>
            </div>
        );
    }
});

var ProductFavorButton = React.createClass({
    addToWish: function(event) {
        event.preventDefault();
        if (this.props.enableClickFunc) {
            TrackAction.addToWish([this.props.chooseID], [this.props.chooseQty], [], [], []);
        }
    },
    render: function() {
        return (
            <div className="addFavor" onClick={this.addToWish}>
                <a href="javascript:void(0);">加入最愛</a>
            </div>
        );
    }
});

var ProductBuyButton = React.createClass({
    addToCart: function(event) {
        event.preventDefault();
        if (this.props.enableClickFunc) {
            TrackAction.addToCart([this.props.chooseID], [this.props.chooseQty], [], [], []);
        }
    },
    render: function() {
        return (
            <div className={this.props.showClass} onClick={this.addToCart}>
                <a href="#">
                    <span>{this.props.showWord}</span>
                </a>
            </div>
        );
    }
});

var ProductFlashTime = React.createClass({

    getInitialState: function() {
        return {days: 0, hours: 0, minutes: 0, seconds: 0, countDownID: 0};
    },

    componentDidMount: function() {
        var flashTime = this.props.flashTime;
        var countDown = this.countDown;
        var countDownID = setInterval(function() {
            countDown(flashTime);
        }, 1000);
        this.setState({countDownID: countDownID});
    },

    componentWillUnmount: function() {
        clearInterval(this.state.countDownID);
    },

    countDown: function(tpeTime) {
        if (typeof tpeTime === 'undefined') {
            clearInterval(this.state.countDownID);
            return;
        }
        var utcNow = new Date().toUTCString();
        var utcEndTime = new Date(tpeTime).toUTCString()
        var totals = Date.parse(utcEndTime) - Date.parse(utcNow);

        if (typeof totals !== "number") {
            clearInterval(this.state.countDownID);
            return;
        }
        if (totals < 1) {
            this.setState({days: 0, hours: 0, minutes: 0, seconds: 0});
            clearInterval(this.state.countDownID);
            this.props.setNotSale(true);
            return;
        }
        var seconds = Math.floor((totals / 1000) % 60);
        var minutes = Math.floor((totals / 1000 / 60) % 60);
        var hours = Math.floor((totals / (1000 * 60 * 60)) % 24);
        var days = Math.floor(totals / (1000 * 60 * 60 * 24));
        this.setState({days: days, hours: hours, minutes: minutes, seconds: seconds});
    },

    leftTime: function() {
        var timeFormat = "{0}天{1}:{2}:{3}";
        timeFormat = timeFormat.replace("{0}", this.state.days);
        timeFormat = timeFormat.replace("{1}", this.state.hours > 9
            ? "" + this.state.hours
            : "0" + this.state.hours);
        timeFormat = timeFormat.replace("{2}", this.state.minutes > 9
            ? "" + this.state.minutes
            : "0" + this.state.minutes);
        timeFormat = timeFormat.replace("{3}", this.state.seconds > 9
            ? "" + this.state.seconds
            : "0" + this.state.seconds);
        return timeFormat;
    },

    render: function() {
        return (
            <div className="snapUp">
                <p>搶購時間</p>
                <div className="snapUpTime">{this.leftTime()}</div>
            </div>
        );
    }
});

var ProductPrice = React.createClass({

    displayPrice: function(price) {
        return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },

    setPriceClass: function() {
        var className = "price";
        if (this.props.priceType == "market") {
            className = "marketPrice";
        }
        return className;
    },

    render: function() {
        return (
            <p className={this.setPriceClass()}>
                <span className="currency">$</span>
                <span className="priceNums">{this.displayPrice(this.props.price)}</span>
            </p>
        );
    }
});

var NEImage = React.createClass({
    getInitialState: function() {
        return {errored: false, isShow: false};
    },

    handleError: function(event) {
        window.removeEventListener("scroll", this.handleScroll);
        this.setState({errored: true});
    },

    detectPosition: function() {
        if (typeof this._reactInternalInstance === 'undefined') {
            return;
        }
        var node = ReactDOM.findDOMNode(this);
        var img = node.getBoundingClientRect();
        var top = img.top;
        var height = img.height;

        // update showImage state if component element is in the viewport
        //var min = window.pageYOffset;
        var innerHeight = window.innerHeight;
        if ((0 <= (top + height) && top <= (innerHeight))) {
            window.removeEventListener("scroll", this.handleScroll);
            this.setState({isShow: true});
        }
    },

    handleScroll: function() {
        if (this.state.isShow) {
            return;
        }
        this.detectPosition();
    },

    componentDidMount: function() {
        var that = this;
        window.addEventListener("scroll", this.handleScroll);
        setTimeout(function() {
            that.detectPosition();
        }, 250);
    },

    componentWillUnmount: function() {
        window.removeEventListener("scroll", this.handleScroll);
    },

    render: function() {
        if (this.state.errored) {
            return <img style={{width: '100px', margin: '0 auto'}} src={ERRORIMGSRC} alt={this.props.alt}/>;
        } else {
            return <img style={this.state.isShow?null:{width: '100px', margin: '0 auto'}} onError={this.handleError} src={this.state.isShow
                ? this.props.src
                : LOADIMGSRC} alt={this.props.alt}/>;
        }
    }
});

var NEProduct = React.createClass({

    propTypes: {
        /*Item ID*/
        productID: React.PropTypes.number.isRequired,
        categoryID: React.PropTypes.number,
        storeID: React.PropTypes.number,
        /*Show discount or not*/
        isDiscount: React.PropTypes.bool,
        /*Item Link*/
        productLink: React.PropTypes.string.isRequired,
        /*Item image url*/
        imgUrl: React.PropTypes.string,
        /*Item image alt*/
        imgAlt: React.PropTypes.string,
        /*Item slogan*/
        slogan: React.PropTypes.string,
        /*show slogan or not*/
        isSlogan: React.PropTypes.bool,
        /*Item title*/
        title: React.PropTypes.string.isRequired,
        /*Item left qty*/
        qty: React.PropTypes.number.isRequired,
        /*show item qty or not*/
        showLeft: React.PropTypes.bool,
        /*Item market price*/
        marketPrice: React.PropTypes.number,
        /*Item selling price*/
        sellingPrice: React.PropTypes.number.isRequired,
        /*set this item can't be sell*/
        notSale: React.PropTypes.bool,
        /*set this item is flash item*/
        isFlash: React.PropTypes.bool,
        /*flash item's countdown time*/
        flashTime: React.PropTypes.string
    },

    getInitialState: function() {
        return {chooseQty: 1, chooseID: this.props.productID, notSale: false}
    },

    _calcuDiscountNumber: function() {
        var discount = "";
        if (typeof this.props.marketPrice === "number" && typeof this.props.sellingPrice === "number" && this.props.marketPrice > 0) {
            var discountNums = Math.round((this.props.sellingPrice / this.props.marketPrice) * 100);
            if (discountNums < 96) {
                discount = discountNums > 9
                    ? (discountNums % 10 == 0
                        ? (discountNums / 10).toString()
                        : discountNums.toString())
                    : "<1";
            }
        }
        return discount;
    },

    setNotSale: function(isSale) {
        this.setState({notSale: isSale});
    },

    setProductClass: function() {
        var className = "prod";
        if (typeof this.props.isDiscount === "boolean" && this.props.isDiscount) {
            className += " prodDiscount";
        }
        if (typeof this.props.qty === "number" && this.props.qty == 0) {
            className += " soldOut";
        }
        return className;
    },

    setBuyButtonClass: function() {
        var className = "prodBtns";
        if (typeof this.props.isFlash === "boolean" && this.props.isFlash) {
            className += " flashSoonBuy";
        }
        return className;
    },

    setDiscountDom: function() {
        var discountDom;
        if (typeof this.props.isDiscount === "boolean" && this.props.isDiscount && this._calcuDiscountNumber() !== "") {
            discountDom = <ProductDisCount discountNums={this._calcuDiscountNumber()}/>;
        }
        return discountDom;
    },

    setPriceDom: function(type, price) {
        var priceDom;
        if (type === "market" && this._calcuDiscountNumber() === "") {
            return priceDom;
        }
        if (typeof price === "number") {
            priceDom = <ProductPrice priceType={type} price={price}/>;
        }
        return priceDom;
    },

    isSlogan: function() {
        var slogan;
        if (typeof this.props.isSlogan === "boolean" && this.props.isSlogan) {
            slogan = <ProductSlogan slogan={this.props.slogan}/>;
        }
        return slogan;
    },

    isComingSoon: function() {
        var comingSoon;
        if ((typeof this.props.notSale === "boolean" && this.props.notSale) || this.state.notSale) {
            comingSoon = <ProductComingSoon/>;
        }
        return comingSoon;
    },

    isShowLeft: function() {
        var showLeft;
        if (typeof this.props.showLeft === "boolean" && this.props.showLeft) {
            showLeft = <ProductShowLeft qty={this.props.qty}/>;
        }
        return showLeft;
    },

    setBuyButton: function() {
        var className = "soonBuy",
            buttonWord = "立即購買",
            enableClickFunc = true;
        if ((typeof this.props.notSale === "boolean" && this.props.notSale) || this.state.notSale) {
            className = "notSale";
            buttonWord = "尚未開賣";
            enableClickFunc = false;
        }
        if (typeof this.props.qty === "number" && this.props.qty == 0) {
            className = " notSale";
            buttonWord = "已售完";
            enableClickFunc = false;
        }
        return <ProductBuyButton showClass={className} showWord={buttonWord} enableClickFunc={enableClickFunc} chooseID={this.state.chooseID} chooseQty={this.state.chooseQty}/>;
    },

    isFlashTime: function() {
        var flashDom,
            enableClickFunc = true;
        if (typeof this.props.isFlash === "boolean" && this.props.isFlash) {
            flashDom = <ProductFlashTime flashTime={this.props.flashTime} setNotSale={this.setNotSale}/>;
        } else {
            flashDom = <ProductFavorButton enableClickFunc={enableClickFunc} chooseID={this.state.chooseID} chooseQty={this.state.chooseQty}/>;
        }
        return flashDom;
    },

    generateProductLink: function(){
        if(this.props.productLink == ""){
          return nemReact.generateUrl("product", {itemid: this.props.productID});
        }else{
          return this.props.productLink;
        }
    },

    render: function() {
        var discountDom = this.setDiscountDom();
        var marketPrice = this.setPriceDom("market", this.props.marketPrice);
        var sellingPrice = this.setPriceDom("selling", this.props.sellingPrice);
        var buyButton = this.setBuyButton();
        return (
            <div className={this.setProductClass()}>
                {discountDom}
                <a href={this.generateProductLink()}>
                    {/*<LazyLoad height={200}>*/}
                    <div className="prodImg">
                        <NEImage key={this.props.imgUrl} src={this.props.imgUrl} alt={this.props.imgAlt}/>
                    </div>
                    {/*</LazyLoad>*/}
                    {this.isSlogan()}
                    <div className="prodName">
                        {this.props.title}
                    </div>
                    <div className="prodPrice">
                        {this.isShowLeft()}
                        {marketPrice}
                        {sellingPrice}
                        {this.isComingSoon()}
                    </div>
                </a>
                <div className={this.setBuyButtonClass()}>
                    {this.isFlashTime()}
                    {buyButton}
                </div>
            </div>

        );
    }
});

window.nemReact.reacts['NEImage'] = NEImage;
module.exports = window.nemReact.reacts['NEProduct'] = NEProduct;



