var AccountStore = nemReact.require('AccountStore');
var AccountAction = nemReact.require('AccountAction');
var GreetingWordsStore = nemReact.require('GreetingWordsStore');
var GreetingWordsAction = nemReact.require('GreetingWordsAction');


function getAccountEmail(){
  return AccountStore.getEmail();
}

function getGreetingWords(){
  return GreetingWordsStore.getGreetingWords();
}

var WelcomeText = React.createClass({

  getInitialState: function() {
    return {
      email: '',
      greetingWord: ''
    };
  },

  componentDidMount: function() {
    AccountStore.addChangeListener(this._onMailChange);
    AccountAction.getEmail();
    GreetingWordsStore.addChangeListener(this._onGreetingWordChange);
    GreetingWordsAction.getGreetingWords();
  },

  componentWillUnmount: function() {
    AccountStore.removeChangeListener(this._onMailChange);
    GreetingWordsStore.removeChangeListener(this._onGreetingWordChange);
  },
  render: function(){
    return (
      <div class="welcomeText">
        <p>Hi, <span className="memberMail">{this.state.email}</span>,您好</p>
        <p className="text">{this.state.greetingWord}</p>
      </div>
    );
  },

  _onMailChange: function(){
    this.setState({email: getAccountEmail()});
  },

  _onGreetingWordChange: function(){
    this.setState({greetingWord: getGreetingWords()});
  }

});

var AccountNavBox = React.createClass({

  stateClick: function(e) {
    e.stopPropagation();
  },

  render: function(){
    return (
      <div className="state accountBox slideDown" onClick={this.stateClick}>
        <div>
          <WelcomeText />
          <ul className="links">
            <li className="favors"><a href={_netwWebURL + nemReact.generateUrl("wish")}><span>最愛清單</span></a></li>
					  <li className="coupons"><a href={_netwWebURL + nemReact.generateUrl("coupon")}><span>折價券</span></a></li>
					  <li className="myOrder"><a href={_netwWebURL + nemReact.generateUrl("order")}><span>我的訂單</span></a></li>
					  <li className="myQA"><a href={_netwWebURL + nemReact.generateUrl("askques")}><span>我要發問</span></a></li>
					  <li className="myAccount"><a href={_netwWebURL + nemReact.generateUrl("editperson")}><span>我的帳號</span></a></li>
					  <li className="Logout"><a href={nemReact.generateUrl("logout")}><span>登出</span></a></li>
          </ul>
        </div>
      </div>
    );
  }
});



module.exports = AccountNavBox;



