/*var CartStore = require('../../stores/cart/CartStore');
var CartAction = require('../../actions/cart/CartAction');*/
var CartStore = nemReact.require('CartStore');
var CartAction = nemReact.require('CartAction');

var CartNavBox = React.createClass({

    stateClick: function(e) {
        e.stopPropagation();
    },

    getInitialState: function() {
        return {domesCart: 0, interCart: 0, chooseCart: 0};
    },

    componentDidMount: function() {
        CartStore.addChangeListener(this._onCartNumberChange);
        CartAction.getCartNumber();
    },

    componentWillUnmount: function() {
        CartStore.removeChangeListener(this._onCartNumberChange);
    },

    render: function() {
        return (
            <div className="state shopBox slideDown" onClick={this.stateClick}>
                <div>
                    <a className="cartBtn" href={_netwWebURL + nemReact.generateUrl("cart", {TypeID: 1})}>
                        <span className="nums">{this.state.domesCart}</span>
                        <span className="cartName">新蛋購物車</span>
                    </a>
                    <a className="cartBtn" href={_netwWebURL + nemReact.generateUrl("cart", {TypeID: 2})}>
                        <span className="nums">{this.state.interCart}</span>
                        <span className="cartName">海外購物車</span>
                    </a>
                    <a className="cartBtn" href={_netwWebURL + nemReact.generateUrl("cart", {TypeID: 3})}>
                        <span className="nums">{this.state.chooseCart}</span>
                        <span className="cartName">任選館購物車</span>
                    </a>
                </div>
            </div>
        );
    },

    _onCartNumberChange: function() {
        this.setState(CartStore.getCartNumber());
    }
});

module.exports = CartNavBox;



